<template>
  <md-card class="md-card-profile">
    <div class="md-card-avatar">
      <img class="img" :src="foto" />
    </div>
    <img
      class="img"
      :src="grado"
      style="max-width: 30%; width: auto; height: auto"
    />
    <div class="md-layout">
      <div class="md-layout-item md-small-size-100 md-size-50">
        <md-field>
          <label>Fecha Graduación</label>
          <md-input v-model="fecha_ingreso" disabled type="text"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-small-size-100 md-size-50">
        <md-field>
          <label>Clasificación</label>
          <md-input v-model="clasificacion" disabled type="text"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-small-size-100 md-size-50">
        <md-field>
          <label>Categoria</label>
          <md-input v-model="categoria" disabled type="text"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-small-size-100 md-size-50">
        <md-field>
          <label>Situación</label>
          <md-input v-model="situacion" disabled type="text"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-small-size-100 md-size-50">
        <md-field>
          <label>Ultimo Ascenso</label>
          <md-input v-model="ultimo_ascenso" disabled type="text"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-small-size-100 md-size-50">
        <md-field>
          <label>Tiempo Servicio</label>
          <md-input v-model="tiempo_servicio" disabled type="text"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-small-size-100 md-size-50">
        <md-field>
          <label>Codigo Componente</label>
          <md-input v-model="codigocomponente" disabled type="text"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-small-size-100 md-size-50">
        <md-field>
          <label>Vencimiento Carnet</label>
          <md-input
            v-model="vencimiento_carnet"
            disabled
            type="text"
          ></md-input>
        </md-field>
      </div>
    </div>
  </md-card>
</template>
<script>
import jwt_decode from "jwt-decode";
export default {
  components: {},
  name: "user-card",
  data() {
    return {
      codigocomponente: "",
      fecha_ingreso: "",
      clasificacion: "",
      categoria: "",
      situacion: "",
      ultimo_ascenso: "",
      tiempo_servicio: "",
      vencimiento_carnet: "",
      foto: "",
      sinFoto: "",
      urlFoto: "",
      grado: "",
      Dmilitar: "",
    };
  },
  mounted() {
    this.$MilitarUserCard()
    const token = sessionStorage.getItem("token");
    const decoded = jwt_decode(token);
    // this.fotografia()
    // this.$imgFotoMilitar(decoded.WUsuario.cedula)
    if (decoded.WUsuario.titular === true) {
      this.foto = `https://app.ipsfa.gob.ve/sssifanb/afiliacion/temp/${decoded.WUsuario.cedula}/foto.jpg`;
    } else {
      this.foto = 'data:image/png;base64,';
    }
    // 
  },
  methods: {
    async fotografia() {
      const token = sessionStorage.getItem("token");
      const decoded = jwt_decode(token);
      const id = decoded.WUsuario.cedula;
      await this.axios
        .get(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/militar/${id}`)
        .then(
          (response) => {
           const fotografiaMil = response.data;
            if (fotografiaMil.Persona.foto === "foto.jpg") {
              this.foto = `https://app.ipsfa.gob.ve/sssifanb/afiliacion/temp/${fotografiaMil.Persona.Datobasico.cedula}/foto.jpg`;  
           } else {
              this.foto = 'data:image/png;base64,';
           }
          },
          (error) => {
            // this.foto = 'data:image/png;base64,';
          this.$Notify("<b>Error!</br> Conexion fallida con el servidor","danger","error"); 
          }
        );
    },
  }
};
</script>
