import Vue from "vue";
import JsPDF from "jspdf";
import "jspdf-autotable";

Vue.prototype.$AutorizacionMedicamentos = function (origen) {
  const datosFamiliar = origen;
  const newDate = new Date();
  const fechax = this.$fecha(newDate);
  const doc = new JsPDF("p", "mm", "a4");
  const pageHeight =
    doc.internal.pageSize.height || doc.internal.pageSize.getHeight();
  const pageWidth =
    doc.internal.pageSize.width || doc.internal.pageSize.getWidth();
  const img = require("../../assets/img/constancias/logo-ipsfa.jpg");
  const fondo = require("../../assets/img/constancias/fondo.png");
  const sello = require("../../assets/img/medicamentos/sello.jpeg");
  const firma = require("../../assets/img/medicamentos/firma.png");

  doc.addImage(img, "PNG", 14, 10, 40, 40);
  doc.addImage(fondo, "PNG", 22, 60, 170, 170);
  doc.addImage(firma, "PNG", 60, 205, 90, 50); // FIRMA
  doc.addImage(sello, "PNG", 10, 205, 60, 40); // SELLO

  doc.setProperties({
    title:
      "AUTORIZACIÓN AFILIADO / TRATAMIENTOS A TRAVÉS DE LOS DIFERENTES CONVENIOS",
    subject: "https://app.ipsfa.gob.ve",
    author: "APP-IPSFA",
    keywords: "generated, javascript, web 2.0, ajax",
    creator: "CAP. ANDRÉS RICARDO RODRÍGUEZ DURÁN",
  });

  doc.setFontSize(10);
  doc.setFont(undefined, "bold");
  doc.text(
    "REPÚBLICA BOLIVARIANA DE VENEZUELA",
    pageWidth / 2,
    pageHeight - 280,
    "center"
  );
  doc.text(
    "MINISTERIO DEL PODER POPULAR PARA LA DEFENSA",
    pageWidth / 2,
    pageHeight - 275,
    "center"
  );
  doc.text(
    "VICEMINISTRO DE SERVICIOS PARA LA DEFENSA",
    pageWidth / 2,
    pageHeight - 270,
    "center"
  );
  doc.text(
    "DIRECCIÓN GENERAL DE EMPRESAS Y SERVICIOS",
    pageWidth / 2,
    pageHeight - 265,
    "center"
  );
  doc.text(
    "FUERZA ARMADA NACIONAL BOLIVARIANA",
    pageWidth / 2,
    pageHeight - 260,
    "center"
  );
  doc.text("RIF: G20003694-3", pageWidth / 2, pageHeight - 255, "center");
  doc.setFont(undefined, "");

  doc.setFontSize(14);
  doc.setFont(undefined, "bold");
  doc.text("AUTORIZACIÓN AFILIADO", pageWidth / 2, pageHeight - 240, "center");
  doc.text(
    "TRATAMIENTOS A TRAVÉS DE LOS DIFERENTES CONVENIOS",
    pageWidth / 2,
    pageHeight - 235,
    "center"
  );
  doc.setFont(undefined, "");
  // INICIO DE TEXTO

  // FIN DE TEXTO
  doc.setFontSize(12);
  doc.text(
    `Quien suscribe ${datosFamiliar.Persona.DatoBasico.nombreprimero} ${datosFamiliar.Persona.DatoBasico.apellidoprimero}, Titular de la Cédula de Identidad Número V-. ${datosFamiliar.Persona.DatoBasico.cedula}, por medio del presente documento autorizó al ciudadano(a) __________________________________________ C.I N° ______________, a retirar en la empresa ______________________________; los MEDICAMENTOS asignados a mi persona para el mes de noviembre de 2021 producto de convenio IPSFA con la mencionada empresa.`,
    14,
    75,
    { maxWidth: 180, align: "justify" }
  );

  doc.setFont(undefined, "bold");
  doc.setFontSize(9);
  doc.text("FIRMA AUTÓGRAFA DEL AFILIADO", 25, 130);
  doc.text("FIRMA DEL AUTORIZADO O HUELLA DACTILAR", 110, 130);
  doc.setFont(undefined, "");

  let col = ["No.", "DOCUMENTOS A CONSIGNAR ADJUNTO A LA AUTORIZACIÓN:"];
  let rows = [];
  let itemNew = [
    { index: "1", id: "COPIA DEL CARNET DE AFILIADO." },
    {
      index: "2",
      id: "COPIA DE LA CÉDULA DE IDENTIDAD DEL AFILIADO Y DEL AUTORIZADO.",
    },
    {
      index: "3",
      id: "RÉCIPE MEDICO EN ORIGINAL. (MEDICAMENTOS CONTROLADOS Y TRATAMIENTO ESPECIAL).",
    },
    {
      index: "4",
      id: "NOTA: SI REQUIERE MEDICAMENTO REFRIGERADO, FAVOR LLEVAR CAVA.",
    },
  ];
  itemNew.forEach((element) => {
    var temp = [element.index, element.id];
    rows.push(temp);
  });
  doc.autoTable({
    head: [col],
    body: rows,
    theme: "grid",
    startY: 145,
    styles: { halign: "justify" },
    })

  doc.setFont(undefined, "bold");
  doc.text(
    "LINDA CLARET GUERRERO VELAZCO",
    pageWidth / 2,
    pageHeight - 35,
    "center"
  );
  doc.text("TENIENTE CORONEL", pageWidth / 2, pageHeight - 30, "center");
  doc.text(
    "GERENTE DE DROGUERIA Y FARMACIAS",
    pageWidth / 2,
    pageHeight - 25,
    "center"
  );
  doc.setFont(undefined, "");

  // FOOTER INICIO
  doc.setFontSize(7);
  doc.text(
    "Dirección: Avenida Los Próceres Edif. Sede del IPSFA. Gerencia de Afiliación Planta Baja. Santa Mónica, municipio Libertador. Caracas, Distrito Capital. Teléfonos:(0212) - 609-23-10 / 609-23-11 /609-23-12",
    14,
    290,
    { maxWidth: 180, align: "justify" }
  );
  // FOOTER FINAL
  doc.autoPrint({ variant: "non-conform" });
  doc.save(`Autorizacion Medicamentos - CI ${datosFamiliar.Persona.DatoBasico.cedula} - ${fechax}.pdf`);
};
