import Vue from "vue";
import JsPDF from "jspdf";
import "jspdf-autotable";

Vue.prototype.$ConstanciaNeto = function (origen, neto, listaCalculo) {
  const datosFamiliar = origen;
  const getDatosNeto = neto;
  const calculosNeto = JSON.parse(neto.calculos);
  const newDate = new Date();
  const fechax = this.$fecha(newDate);
  const doc = new JsPDF("p", "mm", "a4");
  const pageHeight =
    doc.internal.pageSize.height || doc.internal.pageSize.getHeight();
  const pageWidth =
    doc.internal.pageSize.width || doc.internal.pageSize.getWidth();
  const img = require("../assets/img/constancias/logo-ipsfa.jpg");
  const fondo = require("../assets/img/constancias/fondo.png");

  doc.addImage(img, "PNG", 14, 10, 40, 40);
  doc.addImage(fondo, "PNG", 22, 60, 170, 170);

  doc.setProperties({
    title: "CONSTANCIA DE NETO PENSIÓN TITULAR",
    subject: "https://app.ipsfa.gob.ve",
    author: "APP-IPSFA",
    keywords: "generated, javascript, web 2.0, ajax",
    creator: "CAP. ANDRÉS RICARDO RODRÍGUEZ DURÁN",
  });

  doc.setFontSize(10);
  doc.setFont(undefined, "bold");
  doc.text(
    "REPÚBLICA BOLIVARIANA DE VENEZUELA",
    pageWidth / 2,
    pageHeight - 280,
    "center"
  );
  doc.text(
    "MINISTERIO DEL PODER POPULAR PARA LA DEFENSA",
    pageWidth / 2,
    pageHeight - 275,
    "center"
  );
  doc.text(
    "VICEMINISTRO DE SERVICIOS PARA LA DEFENSA",
    pageWidth / 2,
    pageHeight - 270,
    "center"
  );
  doc.text(
    "DIRECCIÓN GENERAL DE EMPRESAS Y SERVICIOS",
    pageWidth / 2,
    pageHeight - 265,
    "center"
  );
  doc.text(
    "FUERZA ARMADA NACIONAL BOLIVARIANA",
    pageWidth / 2,
    pageHeight - 260,
    "center"
  );
  doc.text("RIF: G20003694-3", pageWidth / 2, pageHeight - 255, "center");
  doc.setFont(undefined, "");

  doc.setFontSize(14);
  doc.setFont(undefined, "bold");
  doc.text("MILITAR TITULAR", pageWidth / 2, pageHeight - 240, "center");
  doc.text(
    "RECIBO DE PAGO DE PENSIÓN CORRESPONDIENTE A:",
    pageWidth / 2,
    pageHeight - 235,
    "center"
  );
  doc.text(
    `${getDatosNeto.nomina} - ${getDatosNeto.mes} DEL ${new Date(getDatosNeto.fecha).getFullYear()}`,
    pageWidth / 2,
    pageHeight - 230,
    "center"
  );
  doc.setFont(undefined, "");
  // INICIO DE TEXTO

  let cuentaBanco = ["CUENTA BANCARIA"];
  let rowsCuenta = [];
  let itemCuenta = [
    {
      cuenta: this.$CuentaBancoEncriptar(
        datosFamiliar.Fideicomiso.cuentabancaria
      ),
    },
  ];
  itemCuenta.forEach((element) => {
    let tempCuenta = [element.cuenta];
    rowsCuenta.push(tempCuenta);
  });
  doc.autoTable({
  head: [cuentaBanco],
  body: rowsCuenta,
  startY: 85,
  theme: "grid",
  styles: { halign: "center" },
  })
  let datosMilitar = ["GRADO", "NOMBRES Y APELLIDOS", "N° DE CÉDULA"];
  let rowsMilitar = [];
  let itemMilitar = [
    {
      grado: datosFamiliar.Grado.descripcion,
      nombres:
        datosFamiliar.Persona.DatoBasico.nombreprimero +
        " " +
        datosFamiliar.Persona.DatoBasico.apellidoprimero,
      cedula: datosFamiliar.Persona.DatoBasico.cedula,
    },
  ];
  itemMilitar.forEach((element) => {
    let tempMilitar = [element.grado, element.nombres, element.cedula];
    rowsMilitar.push(tempMilitar);
  });
  doc.autoTable({
    head: [datosMilitar],
    body: rowsMilitar,
    startY: 70,
    theme: "grid",
    styles: { halign: "center" },
    })

  let datosNeto = ["CONCEPTO", "CALCULOS", "ASIGNACIONES", "DEDUCCIONES"];
  let rowsDatoNeto = [];
  let calculos = "";
  let asignaciones = "";
  let deducciones = "";
  let itemNew = calculosNeto.conceptos;
  itemNew.forEach((element) => {
    switch (element.desc) {
      case "sueldo_base":
        element.desc = "SUELDO BASE";
        break;
      case "prima_tiemposervicio":
        element.desc = "PRIMA TIEMPO SERVICIO";
        break;
      case "prima_profesionalizacion":
        element.desc = "PRIMA PROFESIONALIZACION";
        break;
      default:
        element.desc = listaCalculo[element.desc].ABV;
        break;
    }
    switch (element.tipo) {
      case 97: // Calculos
        calculos = this.$FormatearMoneda(element.mont);
        asignaciones = "";
        deducciones = "";
        break;
      case '1': //Asignaciones
        asignaciones = this.$FormatearMoneda(element.mont);
        calculos = "";
        deducciones = "";
        break;
      case 1: //Asignaciones
        asignaciones = this.$FormatearMoneda(element.mont);
        calculos = "";
        deducciones = "";
        break;
      case '3': //Deducciones
        deducciones = this.$FormatearMoneda(element.mont);
        calculos = "";
        asignaciones = "";
        break;
      case '0': //Deducciones
        deducciones = this.$FormatearMoneda(element.mont);
        calculos = "";
        asignaciones = "";
        break;
      default:
        calculos = "",
        deducciones = "",
        asignaciones = ""
        break;
    }
    let tempDatoNeto = [
      element.desc,
      calculos,
      asignaciones,
      deducciones,
    ];
    rowsDatoNeto.push(tempDatoNeto);
  });
  doc.autoTable({
    head: [datosNeto],
    body: rowsDatoNeto,
    startY: 100,
    theme: "grid",
    styles: { halign: "justify" },
  })
  doc.autoTable({
    head: [['TOTAL','ASIGNACIONES','DEDUCCIONES']],
    body: [['TOTAL', `${this.$FormatearMoneda(calculosNeto.asignaciones)}`, `${this.$FormatearMoneda(calculosNeto.deducciones)}`]],
    styles: { halign: "right" },
    theme: "grid",
  })
  doc.autoTable({
    head: [['NETO A COBRAR']],
    body: [[`BS. ${getDatosNeto.neto}`]],
    theme: "grid",
    styles: {
      overflow: "linebreak",
      fontSize: 16,
      valign: "middle",
      halign: "center"
    },
  })
  // doc.rect(10, 220, 190, 20);
  // doc.setFontSize(14);
  // doc.setFont(undefined, "bold");
  // doc.text("NETO A COBRAR", pageWidth / 2, pageHeight - 68, "center");
  // doc.text(
  //   `BS. ${getDatosNeto.neto}`,
  //   pageWidth / 2,
  //   pageHeight - 63,
  //   "center"
  // );
  doc.setFont(undefined, "");
  doc.rect(10, 240, 190, 30);
  doc.setFontSize(14);
  doc.setFont(undefined, "bold");
  doc.text("IPSFA EN CONTACTO", pageWidth / 2, pageHeight - 52, "center");
  doc.setFont(undefined, "");

  // FOOTER INICIO
  doc.setFontSize(7);
  doc.text(
    "Dirección: Avenida Los Próceres Edif. Sede del IPSFA. Gerencia de Afiliación Planta Baja. Santa Mónica, municipio Libertador. Caracas, Distrito Capital. Teléfonos:(0212) - 609-23-10 / 609-23-11 /609-23-12",
    14,
    290,
    { maxWidth: 180, align: "justify" }
  );
  // FOOTER FINAL
  doc.autoPrint({ variant: "non-conform" });
  doc.save(`Constancia Neto - CI ${datosFamiliar.Persona.DatoBasico.cedula} - ${fechax}.pdf`);

  // doc.autoPrint();
  // doc.output("dataurlnewwindow", {filename: `Constancia Neto ${datosFamiliar.Persona.DatoBasico.cedula}.pdf`});
};
