import Vue from "vue";
import axios from "axios";
import jwt_decode from "jwt-decode";

const token = sessionStorage.getItem("token");

Vue.prototype.$MilitarUserCard = async function (origen) {
    const token = sessionStorage.getItem("token");
    const decoded = jwt_decode(token);
    const id = '';
    if (decoded.WUsuario.titular === true) {
       this.id = decoded.WUsuario.cedula; 
    }else{
       this.id = decoded.WUsuario.causante[0].cedula;
    }
    await this.axios.get(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/militar/${this.id}`).then(
        (response) => {
          this.Dmilitar = response.data.Persona.foto;
          (this.codigocomponente = response.data.codigocomponente
            ? response.data.codigocomponente
            : "N/A"),
            (this.fecha_ingreso = this.$ConvertirFechaHumana(
              response.data.fingreso
            )),
            (this.clasificacion = this.$categoria(response.data.categoria)
              ? this.$categoria(response.data.categoria)
              : response.data.categoria),
            (this.categoria = this.$clasificacion(response.data.clase)
              ? this.$clasificacion(response.data.clase)
              : response.data.clase),
            (this.situacion = this.$situacion(response.data.situacion)),
            (this.ultimo_ascenso = this.$ConvertirFechaHumana(
              response.data.fascenso
            )),
            (this.tiempo_servicio = this.$ConvertirAniosConstanciaPension(response.data.tiemposervicio)),
            // (this.tiempo_servicio = response.data.tiemposervicio),
            (this.vencimiento_carnet = this.$ConvertirFechaHumana(
              response.data.Tim.fechavencimiento
            )
              ? this.$ConvertirFechaHumana(response.data.Tim.fechavencimiento)
              : "N/A"),
            (this.grado = this.$grado(response.data.Grado.abreviatura));
        },
        (error) => {
        //   console.log(error.response)
        this.$Notify("<b>Problemas de Conexion</b>, verifique e intente de nuevo", "danger", "error");
        }
    )
};

Vue.prototype.$MilitaryProfile = async function (origen) {
    const token = sessionStorage.getItem("token");
    const decoded = jwt_decode(token);
    const id = '';
    if (decoded.WUsuario.titular === true) {
       this.id = decoded.WUsuario.cedula; 
    }else{
       this.id = decoded.WUsuario.causante[0].cedula;
    }
    await this.axios.get(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/militar/${this.id}`).then(
      (response) => {
          this.datosMilitar = response.data;
          this.dmilitar = response.data;
          this.users = response.data.Familiar;
          this.familia = response.data;
          (this.cedula = response.data.Persona.DatoBasico.cedula),
          (this.nombres = response.data.Persona.DatoBasico.nombreprimero),
          (this.apellidos = response.data.Persona.DatoBasico.apellidoprimero),
          (this.correo = response.data.Persona.Correo.principal
            ? response.data.Persona.Correo.principal
            : "N/A"),
          (this.direccion =
            response.data.Persona.Direccion[0].calleavenida +
            " " +
            response.data.Persona.Direccion[0].casa +
            " " +
            response.data.Persona.Direccion[0].apartamento +
            " " +
            response.data.Persona.Direccion[0].ciudad +
            " " +
            response.data.Persona.Direccion[0].estado +
            " " +
            response.data.Persona.Direccion[0].municipio +
            " " +
            response.data.Persona.Direccion[0].parroquia),
          (this.estado_civil = this.$estadoCivil(
            response.data.Persona.DatoBasico.estadocivil,
            response.data.Persona.DatoBasico.sexo
          )),
          (this.fecha_nacimiento = this.$ConvertirFechaHumana(
            response.data.Persona.DatoBasico.fechanacimiento
          )),
          (this.sexo = this.$sexo(response.data.Persona.DatoBasico.sexo)),
          (this.grado = response.data.Grado.abreviatura),
          (this.componente = response.data.Componente.abreviatura),
          (this.timnombres = response.data.Persona.DatoBasico.nombreprimero),
          (this.timapellidos = response.data.Persona.DatoBasico.apellidoprimero);
          this.gradoA = response.data.Grado.descripcion;
          this.categoria = this.$clasificacion(response.data.clase) ? this.$clasificacion(response.data.clase) : response.data.clase;
      },
      (error) => {
        // console.log(error)
        this.$Notify("<b>Problemas de Conexion</b>, verifique e intente de nuevo", "danger", "error");
      }
    );
};

Vue.prototype.$DataMilitarCompleta = async function (id) {
  await this.axios.get(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/militar/${id}`).then(
      (response) => {
        this.dataMilitarCompleta = response.data
      },
      (error) => {
      this.$Notify("<b>Problemas de Conexion</b>, verifique e intente de nuevo", "danger", "error");
      }
  )
};

Vue.prototype.$ChangePasswordProfile = async function (origen) {
  const Wdata = jwt_decode(sessionStorage.getItem("token"))
  let espacios = false;
  let cont = 0;
  let req = 6;
  while (!espacios && (cont < origen.clave.length)) {
    if (origen.clave.charAt(cont) == " ")
      espacios = true;
    cont++;
  }
  if (!espacios && (req > origen.nueva.length)) {
    this.$Notify(
          `<b>Lo sentimos!<b> La contraseña debe ser mayor a ${req} digitos, verifique e intente de nuevo`,
          "danger",
          "error"
        );
    return false;
  } 
  if (espacios) {
    this.$Notify(
          "<b>Lo sentimos!<b> La contraseña no puede contener espacios en blanco, verifique e intente de nuevo",
          "danger",
          "error"
        );
    return false;
  }
  if (origen.nueva.length == 0 || origen.repetirnueva.length == 0) {
    this.$Notify(
      "<b>Lo sentimos!<b> Los campos de la contraseña no pueden quedar vacios, verifique e intente de nuevo",
      "danger",
      "error"
    );
    return false;
  }
  if (origen.nueva != origen.repetirnueva) {
    this.$Notify(
      "<b>Lo sentimos!<b> Las contraseñas deben de coincidir, verifique e intente de nuevo",
      "danger",
      "error"
    );
    return false;
  } else {
    let data = {
      correo: Wdata.WUsuario.correo,
      cedula: Wdata.WUsuario.cedula,
      clave: origen.clave,
      nueva: origen.nueva,
    };
    await this.axios.post(`${process.env.VUE_APP_RUTA_API}/ipsfa/cambiarclave`,data,
    { headers: {
      "Authorization" : `Bearer ${token}`,
    }},
    (this.loading = true)
    ).then(
    (response) => {
      this.$Notify(
      `<b>${response.data.msj}<b> contraseña actualizada exitosamente`,
      "success",
      "check"
    );
      this.$router.push("/user")
      setTimeout(() => {
          this.loading = false;
        }, 2000)
    },
    (error) => {
      this.$Notify(
      "<b>Lo sentimos!<b> Algo salió mal, verifique e intente de nuevo",
      "danger",
      "error"
    );
    setTimeout(() => {
      this.loading = false;
    }, 2000)
    }
  );
    return true; 
  }
};