<template>
  <div class="content">
    <div class="md-layout">
      <div
        class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
      >
        <md-card class="md-card-profile">
          <div class="md-card-avatar">
            <img class="img" :src="foto" />
          </div>
          <br />
          <md-card-header data-background-color="green">
            <h4 class="title">DATOS BASICOS DEL MILITAR</h4>
            <p class="category">
              <strong
                >{{ grado }} ({{ componente }}) {{ nombres }}
                {{ apellidos }}</strong
              >
            </p>
          </md-card-header>

          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Grado</label>
                  <md-input v-model="gradoA" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Categoria</label>
                  <md-input v-model="categoria" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Nombres</label>
                  <md-input v-model="nombres" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Apellidos</label>
                  <md-input v-model="apellidos" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-33">
                <md-field>
                  <label>Cedula de Identidad</label>
                  <md-input v-model="cedula" disabled></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-67">
                <md-field>
                  <label>Correo Electronico</label>
                  <md-input v-model="correo" disabled type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100"></div>
              <div class="md-layout-item md-small-size-100 md-size-33">
                <md-field>
                  <label>Estado Civil</label>
                  <md-input
                    v-model="estado_civil"
                    disabled
                    type="text"
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-33">
                <md-field>
                  <label>Fecha de Nacimiento</label>
                  <md-input
                    v-model="fecha_nacimiento"
                    disabled
                    type="text"
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-33">
                <md-field>
                  <label>Sexo</label>
                  <md-input v-model="sexo" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Seleccione Neto a Descargar</label>
                  <select
                    name="select"
                    width="100%"
                    @change="
                      constanciaNeto(
                        listaNomina,
                        item.nomina,
                        item.desde,
                        item.hasta
                      )
                    "
                    class="form-control"
                    v-model="item"
                  >
                    <option disabled>SELECCIONE NETO A DESCARGAR</option>
                    <option
                      v-for="item in listaNomina"
                      v-bind:key="item.index"
                      :value="item"
                    >
                      {{ item.nomina }} - {{ item.mes }} -
                      {{ new Date(item.fecha).getFullYear() }} | (Bs
                      {{ item.neto }})
                    </option>
                  </select>
                </md-field>
              </div>

              <!-- <div class="md-layout-item md-size-100 text-center">
            <md-button class="md-raised md-success" @click="constanciaNeto">Descargar Constancia Netos</md-button>
          </div> -->
            </div>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import jwt_decode from "jwt-decode";
export default {
  name: "edit-profile-form",
  props: {
    dataBackgroundColor: {
      type: String,
      default: "",
    },
    tableHeaderColor: {
      type: String,
      default: "",
    },
  },
  components: {
    // SimpleTable,
  },
  data() {
    return {
      item: "",
      dmilitar: [],
      myOptionSelected: "",
      listaNomina: [],
      listaCalculo: [],
      sueldoPensionMil: [],
      foto: "",
      grado: "",
      gradoA: "",
      cedula: "",
      categoria: "",
      nombres: "",
      componente: "",
      apellidos: "",
      correo: "",
      direccion: "",
      estado_civil: "",
      fecha_nacimiento: "",
      sexo: "",
    };
  },
  mounted() {
    this.$MilitaryProfile();
    this.netosList();
    this.listCalculoPension();
    const token = sessionStorage.getItem("token");
    const decoded = jwt_decode(token);
    this.foto = this.$imgFotoMilitar(decoded.WUsuario.cedula);
    this.infoUser = jwt_decode(token);
    this.sobreviviente = this.infoUser.WUsuario.sobreviviente
    if (this.infoUser.WUsuario.titular === true) {
      this.foto = `https://app.ipsfa.gob.ve/sssifanb/afiliacion/temp/${this.infoUser.WUsuario.cedula}/foto.jpg`;
    } else {
      this.foto = 'data:image/png;base64,';
    }
  },
  methods: {
    constanciaNeto(listaNomina, nomina, desde, hasta) {
      let neto = listaNomina;
      let netoGenerado = [];
      for (let i = 0; i < neto.length; i++) {
        let recorrerNetos = neto[i];
        if (
          recorrerNetos.nomina == nomina &&
          recorrerNetos.desde == desde &&
          recorrerNetos.hasta == hasta
        ) {
          netoGenerado.push(recorrerNetos);
        }
      }
      let netoFinal = netoGenerado[0];
      this.$ConstanciaNeto(this.dmilitar, netoFinal, this.listaCalculo);
      this.$Notify("<b>Descarga Exitosa.<b>", "success", "check");
    },
    async netosList() {
      const token = sessionStorage.getItem("token");
      const decoded = jwt_decode(token);
      const id = decoded.WUsuario.cedula;
      await this.axios
        .get(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/pensionado/consultarneto/${id}`)
        .then(
          (response) => {
            this.listaNomina = response.data;
          },
          (error) => {
          this.$Notify("<b>Problemas de Conexion</b>, no se puede generar la constancia, verifique e intente de nuevo mas tarde", "danger", "error");
          }
        );
    },
    async listCalculoPension() {
      const token = sessionStorage.getItem("token");
      const decoded = jwt_decode(token);
      const id = decoded.WUsuario.cedula;
      await this.axios.get(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/pensionado/calculo/${id}`,
      { headers: { "Authorization" : `Bearer ${sessionStorage.getItem("token")}` } }).then(
        (response) => {
          this.listaCalculo = response.data;
        },
        (error) => {
          this.$Notify("<b>Problemas de Conexion</b>, no se puede generar la constancia, verifique e intente de nuevo mas tarde", "danger", "error");
        }
      );
    },
  },
};
</script>
