import Vue from "vue";
import JsPDF from "jspdf";
import "jspdf-autotable";

Vue.prototype.$ConstanciaArc = function (origen, arcConstancia) {
  const today = new Date();
  const year = today.getFullYear();
  const anio = year - 1;
  const datosFamiliar = origen;
  const constanciaARC = arcConstancia.rs;
  const newDate = new Date();
  const fechax = this.$fecha(newDate);
  const doc = new JsPDF("p", "mm", "a4");
  const pageHeight =
    doc.internal.pageSize.height || doc.internal.pageSize.getHeight();
  const pageWidth =
    doc.internal.pageSize.width || doc.internal.pageSize.getWidth();
  const img = require("../assets/img/constancias/logo-ipsfa.jpg");
  const fondo = require("../assets/img/constancias/fondo.png");
  const sello = require("../assets/img/constancias/arc/sello.png");
  const firma = require("../assets/img/constancias/arc/firma.png");

  doc.addImage(img, "PNG", 14, 10, 40, 40);
  doc.addImage(fondo, "PNG", 22, 60, 170, 170);
  doc.addImage(firma, "PNG", 60, 215, 90, 50); // FIRMA
  doc.addImage(sello, "PNG", 10, 215, 50, 50); // SELLO

  doc.setProperties({
    title: "CONSTANCIA DE ARC",
    subject: "https://app.ipsfa.gob.ve",
    author: "APP-IPSFA",
    keywords: "generated, javascript, web 2.0, ajax",
    creator: "CAP. ANDRÉS RICARDO RODRÍGUEZ DURÁN",
  });

  doc.setFontSize(10);
  doc.setFont(undefined, "bold");
  doc.text(
    "REPÚBLICA BOLIVARIANA DE VENEZUELA",
    pageWidth / 2,
    pageHeight - 280,
    "center"
  );
  doc.text(
    "MINISTERIO DEL PODER POPULAR PARA LA DEFENSA",
    pageWidth / 2,
    pageHeight - 275,
    "center"
  );
  doc.text(
    "VICEMINISTRO DE SERVICIOS PARA LA DEFENSA",
    pageWidth / 2,
    pageHeight - 270,
    "center"
  );
  doc.text(
    "DIRECCIÓN GENERAL DE EMPRESAS Y SERVICIOS",
    pageWidth / 2,
    pageHeight - 265,
    "center"
  );
  doc.text(
    "FUERZA ARMADA NACIONAL BOLIVARIANA",
    pageWidth / 2,
    pageHeight - 260,
    "center"
  );
  doc.text("RIF: G20003694-3", pageWidth / 2, pageHeight - 255, "center");
  doc.setFont(undefined, "");

  doc.setFontSize(14);
  doc.setFont(undefined, "bold");
  doc.text(
    "CONSTANCIA AR-C DE PENSIÓN",
    pageWidth / 2,
    pageHeight - 240,
    "center"
  );
  doc.text(`AÑO ${anio}`, pageWidth / 2, pageHeight - 235, "center");
  doc.setFont(undefined, "");
  // INICIO DE TEXTO

  let dMilitar = ["GRADO", "NOMBRES Y APELLIDOS", "N° DE CÉDULA"];
  let col = ["No.", "DESCRIPCION DEL MES", "ASIGNACIONES", "TOTAL"];
  let rows = [];
  let rowsMilitar = [];
  let itemNew = constanciaARC;
  let itemNewMilitar = [
    {
      grado: datosFamiliar.Grado.descripcion,
      nombres:
        datosFamiliar.Persona.DatoBasico.nombreprimero +
        " " +
        datosFamiliar.Persona.DatoBasico.apellidoprimero,
      cedula: datosFamiliar.Persona.DatoBasico.cedula,
    },
  ];
  let i = 0;
  let suma = 0;
  itemNew.forEach((element) => {
    i++;
    suma += parseFloat(element.mont);
    let temp = [
      i,
      this.$Numero_Mes_ARC(element.mes),
      element.mont,
      element.mont,
    ];
    rows.push(temp);
  });
  itemNewMilitar.forEach((element) => {
    let tempMilitar = [element.grado, element.nombres, element.cedula];
    rowsMilitar.push(tempMilitar);
  });
  doc.autoTable({
    head: [col],
    body: rows,
    startY: 85,
    theme: "grid",
    styles: { halign: "justify" },
    })
  doc.autoTable({
    head: [dMilitar],
    body: rowsMilitar,
    startY: 70,
    theme: "grid",
    styles: { halign: "center" },
    })

  doc.autoTable({
    head: [['TOTAL DEVENGADO']],
    body: [[`BS. ${this.$FormatearMoneda(suma)}`]],
    theme: "grid",
    startY: 185,
    styles: {
      overflow: "linebreak",
      fontSize: 16,
      valign: "middle",
      halign: "center"
    },
  })

  doc.setFontSize(12);
  doc.setFont(undefined, "bold");
  doc.text("MATHEUS LOPEZ FERNANDO", pageWidth / 2, pageHeight - 35, "center");
  doc.text("TENIENTE CORONEL", pageWidth / 2, pageHeight - 30, "center");
  doc.text("TESORERO I.P.S.F.A", pageWidth / 2, pageHeight - 25, "center");
  doc.setFont(undefined, "");

  // FOOTER INICIO
  doc.setFontSize(7);
  doc.text(
    "Dirección: Avenida Los Próceres Edif. Sede del IPSFA. Gerencia de Afiliación Planta Baja. Santa Mónica, municipio Libertador. Caracas, Distrito Capital. Teléfonos:(0212) - 609-23-10 / 609-23-11 /609-23-12",
    14,
    290,
    { maxWidth: 180, align: "justify" }
  );
  // FOOTER FINAL
  doc.autoPrint({ variant: "non-conform" });
  doc.save(`Constancia AR-C - CI ${datosFamiliar.Persona.DatoBasico.cedula} - ${fechax}.pdf`);
};
