/* eslint-disable no-console */

import { register } from "register-service-worker";

if (process.env.NODE_ENV === "production") {
  register(`${process.env.BASE_URL}service-worker.js`, {
    ready() {
      console.log(
        "La aplicación está siendo servida desde la caché por un trabajador del servicio\n" +
          "Para obtener más detalles, visite https://goo.gl/AFskqB"
      );
    },
    registered() {
      console.log("El trabajador del servicio ha sido registrado.");
    },
    cached() {
      console.log("El contenido se ha almacenado en caché para su uso sin conexión.");
    },
    updatefound() {
      console.log("Se está descargando contenido nuevo.");
    },
    updated() {
      console.log("Hay nuevo contenido disponible; porfavor refresca.");
    },
    offline() {
      console.log(
        "No se encontró conexión a Internet. La aplicación se está ejecutando en modo fuera de línea."
      );
    },
    error(error) {
      console.error("Error durante el registro del trabajador del servicio:", error);
    },
  });
}
