import Vue from "vue";
import moment from "moment";

Vue.prototype.$Notify = function (msj, type, icon) {
  this.$notify({
    message: msj,
    icon: icon,
    horizontalAlign: "right",
    verticalAlign: "top",
    type: type,
  });
};

Vue.prototype.$fecha = function (origen) {
  return moment(origen).locale("es").format("LL");
};

Vue.prototype.$fecha_constancia = function (origen) {
  return moment(origen).locale("es").calendar();
};

Vue.prototype.$tiempo_servicio = function (origen) {
  let fecha_ingreso = origen;
  let fecha_actual = moment().format("L");
  let tiempo_servicio = "";
  return moment(fecha_actual).locale("es").format("L");
};

Vue.prototype.$estadoCivil = function (estadoCivil, sexo) {
  let estado_civil = "";
  switch (estadoCivil) {
    case "C":
      estado_civil = sexo == "F" ? "CASADA" : "CASADO";
      break;
    case "D":
      estado_civil = sexo == "F" ? "DIVORCIADA" : "DIVORCIADO";
      break;
    case "S":
      estado_civil = sexo == "F" ? "SOLTERA" : "SOLTERO";
      break;
    case "V":
      estado_civil = sexo == "F" ? "VIUDA" : "VIUDO";
      break;
    default:
      estado_civil = "";
      break;
  }
  return estado_civil;
};

