Vue.prototype.$parentesco = function (parent, genero, estadocivil) {
  let parentesco = "";
  switch (parent) {
    case "PD":
      parentesco = genero == "F" ? "MADRE" : "PADRE";
      break;
    case "HJ":
      parentesco = genero == "F" ? "HIJA" : "HIJO";
      break;
    case "EA":
      parentesco = genero == "F" ? "ESPOSA" : "ESPOSO";
      if (estadocivil != undefined) {
        if (estadocivil == "M") {
          parentesco = genero == "F" ? "VIUDA" : "VIUDO";
        }
      }
      break;
    case "HO":
      parentesco = genero == "F" ? "HERMANA" : "HERMANO";
      break;
    default:
      parentesco = "";
      break;
  }
  return parentesco;
};

