Vue.prototype.$ConvertirFechaHumana = function (f) {
  var ISODate = new Date(f).toISOString();
  var fe = ISODate.substr(0, 10);
  var fa = fe.split("-");
  if (fa[0] != "0001") {
    return fa[2] + "/" + fa[1] + "/" + fa[0];
  } else {
    return "";
  }
  //return fa[2] + "/" + fa[1] + "/" + fa[0];
};

Vue.prototype.$ConvertirAniosConstanciaPension = function (origen) {
  let tiemposervicio = origen
  var tp = tiemposervicio.split(" ");
  let ano = tp[0].split("A");
  let mes = tp[1].split("M");
  let dia = tp[2].split("D");
  return ano[0]+' años'+' '+mes[0]+' meses'+' '+dia[0]+' dias'
};

