Vue.prototype.$ConvertirFechaActual = function (origen) {
  let meses = new Array(
    "Enero",
    "Febrero",
    "Marzo",
    "Abril",
    "Mayo",
    "Junio",
    "Julio",
    "Agosto",
    "Septiembre",
    "Octubre",
    "Noviembre",
    "Diciembre"
  );
  let f = origen;
  // console.log(f.getDate() + " del mes de " + meses[f.getMonth()] + " de " + f.getFullYear())
  return (
    f.getDate() +
    " del mes de " +
    meses[f.getMonth()] +
    " de " +
    f.getFullYear()
  );
};

Vue.prototype.$ConvertirFechaEdad = function (origen) {
  let fingresoconvertida = origen.fingreso;
  // console.log(fingresoconvertida)
  let fretiroconvertida = origen.fretiro;
  // console.log(fretiroconvertida)
  let fecha = fingresoconvertida;
  let fecha_retiro = fretiroconvertida;
  var arr = [];
  var fecha_r = fecha_retiro.split("-");
  var ano_r = fecha_r[0];
  var mes_r = fecha_r[1];
  var dia_r = fecha_r[2];

  var list = fecha.split("-");
  var ano = list[0];
  var mes = list[1];
  var dia = list[2];

  var dia_dif,
    mes_dif,
    ano_dif = 0;

  if (dia_r < dia) {
    dia_dif = dia_r + 30 - dia; //27 -5
    mes_r--;
  } else {
    dia_dif = dia_r - dia; //27 -5
  }

  if (mes_r < mes) {
    mes_dif = mes_r + 12 - mes; //27 -5
    ano_r--;
  } else {
    mes_dif = mes_r - mes;
  }

  ano_dif = ano_r - ano;
  arr["e"] = ano_dif;

  if (mes_dif > 5) {
    arr["n"] = ano_dif + 1;
  } else {
    arr["n"] = ano_dif;
  }

  // console.log(arr)
  return arr;
};

Vue.prototype.$CuentaBancoEncriptar = function (origen) {
  let cadena = origen;
  let max = cadena.length;
  let derecha = cadena.substring(0, 4);
  let izquierda = cadena.substring(16);
  return derecha + "****************" + izquierda;
};

Vue.prototype.$FormatearMoneda = function (origen) {
  let tmp = parseFloat(origen.toString()).toFixed(2);
  return tmp;
};

