Vue.prototype.$SituacionFamiliarConstanciaAfiliacion = function (estatuscarnet,condicion,estudia,beneficio,esmilitar,fechadefuncion) {
  // var origen = [estatuscarnet,condicion,estudia,beneficio,esmilitar]
  switch (esmilitar) {
    case false:
      esmilitar = "ACTIVO";
      break;
    case true:
      esmilitar = "INACTIVO";
      break;
    default:
      break;
  }
  if (beneficio === false) {
    esmilitar = "INACTIVO";
  }
  if (fechadefuncion != "0001-01-01T00:00:00Z") {
    esmilitar = "FALLECIDO";
  }
  return esmilitar;
};

Vue.prototype.$SeleccionarFamiliar = function (objFam, id) {
    let familiar = '';
    objFam.forEach(e => {
      let Prs = e.Persona;    
      if( Prs.DatoBasico.cedula == id ){      
        familiar = e;
      }
    });
    return familiar;
};

Vue.prototype.$ConvertirFechaActualX = function () {
  var meses = new Array("enero", "febrero", "marzo", "abril", "mayo", "junio",
      "julio", "agosto", "septiembre", "octubre", "noviembre", "diciembre");
  var f = new Date();
  return f.getDate() + " días del mes de " + meses[f.getMonth()] + " de " + f.getFullYear();
};
