import Vue from "vue";
import axios from "axios";
import jwt_decode from "jwt-decode";

const token = sessionStorage.getItem("token");


Vue.prototype.$Login = async function (origen) {
    try {
        (this.loading = true)
    const response = await this.axios.post(`${process.env.VUE_APP_RUTA_API}/ipsfa/loginW`,origen)
    const userData = response.data;
    const decoded = jwt_decode(userData.token);
    sessionStorage.setItem("token", userData.token);
    this.$Notify(
        `<b>Bienvenido al IPSFA En Línea.</br> ${decoded.WUsuario.nombre} ${decoded.WUsuario.apellido}<b>`,
        "success",
        "check"
      );
      if (decoded.WUsuario.titular == true) {
        this.$router.push("/user");
      } else {
        this.$router.push("/survivor");
      }
      setTimeout(() => {
        this.loading = false;
      }, 2000)
    }
    catch (error) {
        this.$Notify(
            `<b>${error.response.data}</br>`,
            "danger",
            "error"
          ); 
          setTimeout(() => {
            this.loading = false;
          }, 2000)
    }
}

Vue.prototype.$Logout = function () {
  sessionStorage.removeItem("token");
  sessionStorage.removeItem("ChangePassword");
  this.$router.push("/");
  this.$Notify("<b>Desconectando...!</b>", "success", "error");
}

Vue.prototype.$Register = async function (origen) {
  try {
    const response = await this.axios.post(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/identificacion`,origen,
      (this.loading = true),
    );
    this.$Notify(
      "<b>Usuario Registrado Exitosamente<b>",
      "success",
      "check"
    );
    this.$router.push("/");
    setTimeout(() => {
      this.loading = false;
    }, 2000)
  } catch (error) {
    this.$Notify(
      `<b>Error!</b> ${error.response.data.msj}`,
      "danger",
      "check"
    );
    // setTimeout(() => {
      this.loading = false;
    // }, 1000)
  }

};