<template>
  <div class="content">
    <div class="md-layout">
      <div
        class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
      >
        <md-card class="md-card-profile">
          <div class="md-card-avatar">
            <img class="img" :src="foto" />
          </div>
          <br />
          <md-card-header data-background-color="green">
            <h4 class="title">DATOS BASICOS DEL MILITAR</h4>
            <p class="category">
              <strong
                >{{ grado }} ({{ componente }}) {{ nombres }}
                {{ apellidos }}</strong
              >
            </p>
          </md-card-header>

          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Grado</label>
                  <md-input v-model="gradoA" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Categoria</label>
                  <md-input v-model="categoria" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Nombres</label>
                  <md-input v-model="nombres" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Apellidos</label>
                  <md-input v-model="apellidos" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-33">
                <md-field>
                  <label>Cedula de Identidad</label>
                  <md-input v-model="cedula" disabled></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-67">
                <md-field>
                  <label>Correo Electronico</label>
                  <md-input v-model="correo" disabled type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100"></div>
              <div class="md-layout-item md-small-size-100 md-size-33">
                <md-field>
                  <label>Estado Civil</label>
                  <md-input
                    v-model="estado_civil"
                    disabled
                    type="text"
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-33">
                <md-field>
                  <label>Fecha de Nacimiento</label>
                  <md-input
                    v-model="fecha_nacimiento"
                    disabled
                    type="text"
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-33">
                <md-field>
                  <label>Sexo</label>
                  <md-input v-model="sexo" disabled type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-100 text-center">
                <md-button class="md-raised md-success" v-if="this.sobreviviente === true" @click="PensionSobreviviente" v-show="btnGenerarConstanciaPensionSobreviviente" > Descargar Constancia Pensión Sobreviviente</md-button>
                <md-button class="md-raised md-success" v-else @click="constanciaPension" v-show="btnGenerarConstanciaPension"> Descargar Constancia Pensión </md-button>
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import jwt_decode from "jwt-decode";
export default {
  name: "edit-profile-form",
  props: {
    dataBackgroundColor: {
      type: String,
      default: "",
    },
    tableHeaderColor: {
      type: String,
      default: "",
    },
  },
  components: {
    // SimpleTable,
  },
  data() {
    return {
      btnGenerarConstanciaPensionSobreviviente: '',
      btnGenerarConstanciaPension: '',
      dmilitar: [],
      sueldoPensionMil: [],
      dataPensionSobreviviente: [],
      foto: "",
      grado: "",
      gradoA: "",
      cedula: "",
      categoria: "",
      nombres: "",
      componente: "",
      apellidos: "",
      correo: "",
      direccion: "",
      estado_civil: "",
      fecha_nacimiento: "",
      sexo: "",
      infoUser: '',
      titular: '',
      sobreviviente: ''
    };
  },
  mounted() {
    this.$MilitaryProfile();
    const token = sessionStorage.getItem("token");
    this.infoUser = jwt_decode(token);
    this.sobreviviente = this.infoUser.WUsuario.sobreviviente
    if (this.infoUser.WUsuario.titular === true) {
      this.foto = `https://app.ipsfa.gob.ve/sssifanb/afiliacion/temp/${this.infoUser.WUsuario.cedula}/foto.jpg`;
      this.sueldoPension();
    } else {
      this.foto = 'data:image/png;base64,';
      this.sueldoPensionSobreviviente();
    }
  },
  methods: {
    constanciaPension() {
      this.$ConstanciaPension(this.dmilitar, this.sueldoPensionMil);
      this.$Notify("<b>Descarga Exitosa.<b>", "success", "check");
    },
    PensionSobreviviente() {
      this.$ConstanciaPensionSobreviviente(this.dmilitar, this.dataPensionSobreviviente);
      this.$Notify("<b>Descarga Exitosa.<b>", "success", "check");
    },
    async sueldoPension() {
      const token = sessionStorage.getItem("token");
      const decoded = jwt_decode(token);
      const id = decoded.WUsuario.cedula;
      await this.axios.get(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/pensionado/calculo/${id}`,
      { headers: { "Authorization" : `Bearer ${sessionStorage.getItem("token")}` } }).then(
        (response) => {
           this.sueldoPensionMil = response.data;
           this.btnGenerarConstanciaPension = true
        },
        (error) => {
          this.btnGenerarConstanciaPension = false
          this.$Notify("<b>Problemas de Conexion</b>, no se puede generar la constancia, verifique e intente de nuevo mas tarde", "danger", "error");
        }
      );
    },
    async sueldoPensionSobreviviente() {
      const token = sessionStorage.getItem("token");
      const decoded = jwt_decode(token);
      const idFamilitar = decoded.WUsuario.cedula;
      const idTitular = decoded.WUsuario.causante[0].cedula;
        await this.axios.get(`${process.env.VUE_APP_RUTA_API}/ipsfa/api/web/pensionado/calculo/${idTitular}`,
        { headers: { "Authorization" : `Bearer ${sessionStorage.getItem("token")}` } }).then(
        (response) => {
          this.btnGenerarConstanciaPensionSobreviviente = true
          this.dataPensionSobreviviente = response.data;
        },
        (error) => {
          this.btnGenerarConstanciaPensionSobreviviente = false
          this.$Notify("<b>Problemas de Conexion</b>, no se puede generar la constancia, verifique e intente de nuevo mas tarde", "danger", "error");
        }
      );
    },
  },
};
</script>
