<script>
import jwt_decode from "jwt-decode";
export default {
  name: "edit-profile-form",
  props: {
    dataBackgroundColor: {
      type: String,
      default: "",
    },
    tableHeaderColor: {
      type: String,
      default: "",
    },
  },
  components: {
    // SimpleTable,
  },
  data() {
    return {
      dmilitar: [],
      sueldoPensionMil: [],
      foto: "",
      grado: "",
      gradoA: "",
      cedula: "",
      categoria: "",
      nombres: "",
      componente: "",
      apellidos: "",
      correo: "",
      direccion: "",
      estado_civil: "",
      fecha_nacimiento: "",
      sexo: "",
    };
  },
  mounted() {
    this.$MilitaryProfile();
    this.sueldoPension();
    const token = sessionStorage.getItem("token");
    const decoded = jwt_decode(token);
    if (decoded.WUsuario.titular === true) {
      this.foto = `https://app.ipsfa.gob.ve/sssifanb/afiliacion/temp/${decoded.WUsuario.cedula}/foto.jpg`;
    } else {
      this.foto = 'data:image/png;base64,';
    }  
  },
  methods: {
    constanciaPension() {
    const token = sessionStorage.getItem("token");
    const decoded = jwt_decode(token);
      this.$ConstanciaPensionSobreviviente(this.dmilitar, this.sueldoPensionMil);
      this.$Notify("<b>Descarga Exitosa.<b>", "success", "check");
    },
    async sueldoPension() {
      const token = sessionStorage.getItem("token");
      const decoded = jwt_decode(token);
      const idFamilitar = decoded.WUsuario.cedula;
      const idTitular = decoded.WUsuario.causante[0].cedula;
      await this.axios.get(`/ipsfa/api/pensionado/consultarsobreviviente/${idTitular}/${idFamilitar}`).then(
        (response) => {
          console.log(response.data)
          this.sueldoPensionMil = response.data;
        },
        (error) => {
          // console.log(error)
        }
      );
    },
  },
};
</script>
