import Vue from "vue";
import JsPDF from "jspdf";
import "jspdf-autotable";

Vue.prototype.$Constancia = function (origen) {
  const datosFamiliar = origen;
  const newDate = new Date();
  const fechax = this.$fecha(newDate);
  const doc = new JsPDF("p", "mm", "a4");
  const pageHeight =
    doc.internal.pageSize.height || doc.internal.pageSize.getHeight();
  const pageWidth =
    doc.internal.pageSize.width || doc.internal.pageSize.getWidth();
  const img = require("../../assets/img/constancias/logo-ipsfa.jpg");
  const fondo = require("../../assets/img/constancias/fondo.png");
  const sello = require("../../assets/img/constancias/sello.png");
  const firma = require("../../assets/img/constancias/firma.png");
  const sinFoto = require("../../assets/img/constancias/sin-foto.png");
  // const foto = require('../../assets/img/constancias/sin-foto.png')
  const foto = this.$imgFotoMilitar(datosFamiliar.Persona.DatoBasico.cedula);
  // const foto = `https://app.ipsfa.gob.ve/sssifanb/afiliacion/temp/${datosFamiliar.Persona.DatoBasico.cedula}/foto.jpg`
  const grado = require(`../../assets/img/grados/${datosFamiliar.Grado.abreviatura.toLowerCase()}.png`);

  doc.addImage(img, "PNG", 14, 10, 40, 40);
  doc.addImage(foto, "PNG", 165, 10, 30, 30);
  doc.addImage(grado, "PNG", 171, 40, 20, 10);
  doc.addImage(fondo, "PNG", 22, 60, 170, 170);
  doc.addImage(firma, "PNG", 60, 205, 90, 50); // FIRMA
  doc.addImage(sello, "PNG", 10, 205, 60, 40); // SELLO

  doc.setProperties({
    title: "CONSTANCIA DE AFILIACIÓN",
    subject: "https://app.ipsfa.gob.ve",
    author: "APP-IPSFA",
    keywords: "generated, javascript, web 2.0, ajax",
    creator: "CAP. ANDRÉS RICARDO RODRÍGUEZ DURÁN",
  });

  doc.setFontSize(10);
  doc.setFont(undefined, "bold");
  doc.text(
    "REPÚBLICA BOLIVARIANA DE VENEZUELA",
    pageWidth / 2,
    pageHeight - 280,
    "center"
  );
  doc.text(
    "MINISTERIO DEL PODER POPULAR PARA LA DEFENSA",
    pageWidth / 2,
    pageHeight - 275,
    "center"
  );
  doc.text(
    "VICEMINISTRO DE SERVICIOS PARA LA DEFENSA",
    pageWidth / 2,
    pageHeight - 270,
    "center"
  );
  doc.text(
    "DIRECCIÓN GENERAL DE EMPRESAS Y SERVICIOS",
    pageWidth / 2,
    pageHeight - 265,
    "center"
  );
  doc.text(
    "FUERZA ARMADA NACIONAL BOLIVARIANA",
    pageWidth / 2,
    pageHeight - 260,
    "center"
  );
  doc.text("RIF: G20003694-3", pageWidth / 2, pageHeight - 255, "center");
  doc.setFont(undefined, "");

  doc.setFontSize(14);
  doc.setFont(undefined, "bold");
  doc.text(
    "CONSTANCIA DE AFILIACIÓN",
    pageWidth / 2,
    pageHeight - 240,
    "center"
  );
  doc.setFont(undefined, "");
  // INICIO DE TEXTO

  // FIN DE TEXTO
  doc.setFontSize(10);
  doc.setFont(undefined, "bold");
  let militar = 
  doc.setFont(undefined, "");
  doc.setFontSize(12);
  doc.text(
    `Quien suscribe GENERAL DE DIVISIÓN Presidende de la Junta Administradora del Instituto de Previsión Social de la Fuerza Armada Nacional Bolivariana, hace constar por medio de la presente que el (la) CIUDADANO (A) ${datosFamiliar.Grado.descripcion} ${datosFamiliar.Persona.DatoBasico.nombreprimero} ${datosFamiliar.Persona.DatoBasico.apellidoprimero}, Titular de la Cédula de Identidad Número V-. ${datosFamiliar.Persona.DatoBasico.cedula}, se encuentra Afiliado(a) a este Instituto.`,
    14,
    65,
    { maxWidth: 180, align: "justify" }
  );

  let columns = [
    "NOMBRES Y APELLIDOS",
    "CEDULA",
    "PARENTESCO",
    "FECHA NAC.",
    "SITUACIÓN",
  ];
  let rows = [];
  // console.log(datosFamiliar)
  let itemNew = datosFamiliar.Familiar;
  itemNew.forEach((element) => {
    let temp = [
      element.Persona.DatoBasico.nombreprimero +' '+element.Persona.DatoBasico.apellidoprimero,
      element.Persona.DatoBasico.cedula,
      this.$parentesco(
        element.parentesco,
        element.Persona.DatoBasico.sexo,
        element.Persona.DatoBasico.estadocivil
      ),
      this.$fecha_constancia(element.Persona.DatoBasico.fechanacimiento),
      // this.$SituacionFamiliarConstanciaAfiliacion(element.estatuscarnet),
      this.$SituacionFamiliarConstanciaAfiliacion(element.estatuscarnet, element.condicion, element.estudia, element.beneficio, element.esmilitar, element.Persona.DatoBasico.fechadefuncion)
    ];
    rows.push(temp);
  });
  doc.autoTable({
    head: [columns],
    body: rows,
    theme: "grid",
    margin: { top: 90 },
    startY: 90,
    styles: {
      overflow: "linebreak",
      fontSize: 9,
      valign: "middle",
    },
    })

  doc.setFontSize(12);
  doc.text(
    `Constancia que expide a petición de la parte interesada a los ${fechax}`,
    14,
    195
  );
  doc.setFontSize(12);
  doc.setFont(undefined, "bold");
  doc.text(
    "ENRIQUE JOSÉ AROCHA RIVAS",
    pageWidth / 2,
    pageHeight - 55,
    "center"
  );
  doc.text("GENERAL DE DIVISIÓN", pageWidth / 2, pageHeight - 50, "center");
  doc.text(
    "PRESIDENTE DE LA JUNTA ADMINISTRADORA I.P.S.F.A",
    pageWidth / 2,
    pageHeight - 45,
    "center"
  );

  doc.text(
    "CHAVEZ VIVE LA PATRÍA SIGUE",
    pageWidth / 2,
    pageHeight - 35,
    "center"
  );
  doc.text(
    "INDEPENDENCIA Y PATRIA SOCIALISTA... VIVIREMOS Y VENCEREMOS",
    pageWidth / 2,
    pageHeight - 30,
    "center"
  );
  doc.text(
    '"LEALES SIEMPRE, TRAIDORES NUNCA"...',
    pageWidth / 2,
    pageHeight - 25,
    "center"
  );
  doc.setFont(undefined, "");

  // FOOTER INICIO
  doc.setFontSize(7);
  doc.text(
    "Dirección: Avenida Los Próceres Edif. Sede del IPSFA. Gerencia de Afiliación Planta Baja. Santa Mónica, municipio Libertador. Caracas, Distrito Capital. Teléfonos:(0212) - 609-23-10 / 609-23-11 /609-23-12",
    14,
    290,
    { maxWidth: 180, align: "justify" }
  );
  // FOOTER FINAL
    doc.autoPrint({ variant: "non-conform" });
    doc.save(`Constancia de Afiliacion - CI ${datosFamiliar.Persona.DatoBasico.cedula} - ${fechax}.pdf`);  

};
