<template>
  <div class="content">
    <div class="md-layout">
      <div
        class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
      >
        <md-card class="md-card-profile">
          <div class="md-card-avatar">
            <img class="img" :src="foto" />
          </div>
          <br />
          <md-card-header data-background-color="green">
            <h4 class="title">DATOS BASICOS DEL MILITAR</h4>
            <p class="category">
              <strong
                >{{ grado }} ({{ componente }}) {{ nombres }}
                {{ apellidos }}</strong
              >
            </p>
          </md-card-header>

          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field md-has-password>
                  <label>Constraseña Actual</label>
                  <md-input v-model="Password"  type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field md-has-password>
                  <label>Nueva Contraseña</label>
                  <md-input v-model="NewPassword"  type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field md-has-password>
                  <label>Repetir Nueva Contraseña</label>
                  <md-input v-model="RepeatNewPassword"  type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-100 text-center">
                <md-button class="md-raised md-success" @click="changePassword">Cambiar Contraseña</md-button
                >
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>
    </div>
    <div class="loading-overlay" v-if="loading">
              <md-progress-spinner
                md-mode="indeterminate"
                :md-stroke="2"
              ></md-progress-spinner>
            </div>
  </div>
</template>

<script>
import jwt_decode from "jwt-decode";
export default {
  name: "edit-profile-form",
  props: {
    dataBackgroundColor: {
      type: String,
      default: "",
    },
    tableHeaderColor: {
      type: String,
      default: "",
    },
  },
  components: {
    // SimpleTable,
  },
  data() {
    return {
      loading: false,
      dmilitar: [],
      foto: "",
      grado: "",
      componente: "",
      nombres: "",
      apellidos: "",
      Password: "",
      NewPassword: "",
      RepeatNewPassword: "",
    };
  },
  mounted() {
    this.$MilitaryProfile();
    const token = sessionStorage.getItem("token");
    const decoded = jwt_decode(token);
    if (decoded.WUsuario.titular === true) {
      this.foto = `https://app.ipsfa.gob.ve/sssifanb/afiliacion/temp/${decoded.WUsuario.cedula}/foto.jpg`;
    } else {
      this.foto = 'data:image/png;base64,';
    }
  },
  methods: {
    async changePassword() {
      let data = {clave: this.Password, nueva: this.NewPassword, repetirnueva: this.RepeatNewPassword,};
      this.$ChangePasswordProfile(data)
    },
  },
};
</script>
