import Vue from "vue";
import axios from "axios";
import jwt_decode from "jwt-decode";

const token = sessionStorage.getItem("token");

Vue.prototype.$ForgotPassword = async function (origen) {
    (this.loading = true)
    await this.axios.post(`${process.env.VUE_APP_RUTA_API}/ipsfa/recuperarclave`,origen).then(
        (response) => {
          this.$Notify(
          "<b>Se ha enviado un link valido por 15 minutos!<b>",
          "success",
          "check"
        );
        sessionStorage.setItem("token", response.data.token);
        sessionStorage.setItem("ChangePassword", response.data.token);
        this.$router.push("/passwordchange");
        setTimeout(() => {
            this.loading = false;
          }, 2000)
        },
        (error) => {
          this.$Notify(
          `<b>${error.response.data}<b>`,
          "danger",
          "error"
        );
        setTimeout(() => {
            this.loading = false;
          }, 2000)
        }
      );

};

Vue.prototype.$ChangeNewForgotPassword = async function (origen) {
  const Wdata = jwt_decode(sessionStorage.getItem("ChangePassword"))
  let espacios = false;
  let cont = 0;
  let req = 6;
  while (!espacios && (cont < origen.clave.length)) {
    if (origen.clave.charAt(cont) == " ")
      espacios = true;
    cont++;
  }
  if (!espacios && (req > origen.clave.length)) {
    this.$Notify(
          `<b>Lo sentimos!<b> La contraseña debe ser mayor a ${req} digitos, verifique e intente de nuevo`,
          "danger",
          "error"
        );
    return false;
  } 
  if (espacios) {
    this.$Notify(
          "<b>Lo sentimos!<b> La contraseña no puede contener espacios en blanco, verifique e intente de nuevo",
          "danger",
          "error"
        );
    return false;
  }
  if (origen.clave.length == 0 || origen.nuevaclave.length == 0) {
    this.$Notify(
      "<b>Lo sentimos!<b> Los campos de la contraseña no pueden quedar vacios, verifique e intente de nuevo",
      "danger",
      "error"
    );
    return false;
  }
  if (origen.clave != origen.nuevaclave) {
    this.$Notify(
      "<b>Lo sentimos!<b> Las contraseñas deben de coincidir, verifique e intente de nuevo",
      "danger",
      "error"
    );
    return false;
  } else {
  const data = {
      correo: Wdata.WUsuario.correo,
      cedula: Wdata.WUsuario.cedula,
      clave: origen.clave
 };
    await this.axios.post(`${process.env.VUE_APP_RUTA_API}/ipsfa/restablecerclave`,data,
    { headers: {
      "Authorization" : `Bearer ${sessionStorage.getItem("token")}`,
    }},
    (this.loading = true)
    ).then(
    (response) => {
      this.$Notify(
      `<b>${response.data.msj}<b>`,
      "success",
      "check"
    );
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("ChangePassword");    
      this.$router.push("/")
      setTimeout(() => {
          this.loading = false;
        }, 2000)
    },
    (error) => {
      this.$Notify(
      "<b>Lo sentimos!<b> Algo salió mal, verifique e intente de nuevo",
      "danger",
      "error"
    );
    setTimeout(() => {
      this.loading = false;
    }, 2000)
    }
  );
    return true; 
  }
};